<template>
    <!-- 站点详情=》实时参数 -->
  <div class="real-time-param">
    <el-table
      :data="tableData"
      height="100%"
      border
      style="width: 100%">
      <el-table-column
        prop="instrument"
        label="仪器">
      </el-table-column>
      <el-table-column
        prop="parameter"
        label="参数">
      </el-table-column>
      <el-table-column
        prop="company"
        label="单位">
      </el-table-column>
      <el-table-column
        prop="state"
        label="状态">
      </el-table-column>
      <el-table-column
        prop="currentValue"
        label="当前值">
      </el-table-column>
      <el-table-column
        prop="normalRange"
        label="正常范围">
      </el-table-column>
      <el-table-column
        prop="uploadTime"
        label="上传时间">
      </el-table-column>
      <el-table-column
        prop="modificationRecord"
        label="修改记录">
      </el-table-column>
      <el-table-column
        prop="operation"
        label="操作">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
    export default {
        name: "realTimeParam",
        data(){
          return{
            tableData:[]
          }
        }
    }
</script>

<style scoped lang="less">
.real-time-param{
  height: 466px;
  padding: 7px;
}
</style>



